
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
/*输入框样式*/
.addInput{
  width:233px;
}
/*todo列表样式*/
#list{
  padding:0;
}
#list li{
  width:233px;
  height:20px;
  margin:10px auto;
  list-style: none;
  border-radius: 2px;
  text-align: left;
}
#list li.finished{
  text-decoration: line-through;
  color:darkgrey;
}
#list li.finished span{
  text-decoration: line-through;
  color:darkgrey;
}
#list li.finished span:hover{
  cursor:pointer;
  text-decoration: none;
  color: #2c3e50;
}
#list li span{
  float: right;
}



